.offers-main{
    margin: 80px 80px;
    flex-wrap: wrap;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-gap: 10px;
    justify-content: center;
    align-items: center;
    
}
.myImage{
    width: 122px;
    height: 122px;
    background: #D9D9D9;
    border-radius: 50%;
}
.offers-main-1 h3{
    padding: 8px;
}
.offers-main-2 p{
    padding: 8px;
}
.offers-button{
    margin: 7px 7px;
}
@media only screen and (max-width: 1000px){
    .myImage{
        /* padding: px; */
        width:100%;  
        height: 100%;
    }
    .offers-main{
        grid-template-columns: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

}

.show-offer-div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width:100vw;    
    padding:10px 50px;
    overflow-x: scroll;
}
/* .show-offer-div::-webkit-scrollbar{
    display: none;
  } */

.show-offer-div h1{
    margin:40px auto;
}


.show-offer-table{
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
    padding:10px 30px;
    border-radius: 10px;
    max-width: -moz-fit-content;
    max-width: fit-content;
    margin:10px auto;
    table-layout: fixed;
    /* overflow-x: auto; */
    /* white-space: nowrap; */
}
.show-offer-table td, .show-offer-table th{
    padding:20px 5px;
}


.show-offer-table tr:nth-child(even){
    background-color: #D9D9D9;
}

.cand-ordered-list{
    display: block;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    background-color: #f7f5f5;
    padding:5px;
    border-radius: 5px;
    cursor:pointer
}

.cand-ordered-list:hover{
    background-color: #D9D9D9;
}

@media only screen and (max-width:600px) {
    .offers-main{
        padding:10px;
        margin:20px;
        border-radius: 5px;
    }
}