

.admin-user-container{
    display: flex;
    width:100vw;
    flex-wrap: wrap;
    margin: auto;
    align-items: center;
    justify-content: center;
    gap:20px;
    padding:30px;
}

.admin-user-buttons{
    padding: 40px 100px;
    background-color:rgb(241, 241, 241);
    font-size: 30px;
    border-radius: 10px;
}

.admin-user-buttons:hover{
    cursor:pointer;
    background-color: rgb(235, 234, 234);
    color: rgb(5,247,85);
}