.candidate-offers-container{
    margin:40px 100px;
}

.offers-heading{
    text-align: center;
    font-size: 40px;
}

.candidate-offers{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:40px;
    flex-wrap: wrap;
    margin:50px auto;
}

.candidate-offer{
    display: flex;
    align-items: center;
    gap:20px;
    flex-wrap: wrap;
    background-color:rgb(238, 238, 238);
    padding:10px 20px;
    border-radius: 10px;
}

.offer-image{
    margin:auto 30px;
}


.offer-desc{
    margin:auto 30px;
}

.offer-buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 100%; */
}


@media only screen and (max-width:1000px){
    .candidate-offers-container{
        margin:40px 10px;
        padding:0px;
    }
    .candidate-offers{
        overflow-x: auto;
    }
    .offer-desc{
        text-align: center;
    }
    .offer-buttons{
        width: 100%;
    }

}

@media only screen and (max-width:350px){
    .candidate-offer h1{
        text-align: center;
    }
}